text.beginPath();\n          drawCell(polygons[0]);\n          context.fillStyle = \"#f00\";\n          context.fill();\n\n          context.beginPath();\n          for (var i = 0, n = polygons.length; i < n; ++i){\n            drawCell(polygons[i]);\n          }\n          context.strokeStyle = \"#00f\";\n          context.stroke();\n        }\n\n        function drawCell(cell) {\n          context.save();\n          if (!cell) return false;\n          context.moveTo(cell[0][0], cell[0][1]);\n          for (var j = 1, m = cell.length; j < m; ++j) {\n            context.lineTo(cell[j][0], cell[j][1]);\n          }\n          context.closePath();\n          context.clip();\n          context.drawImage(img, cell[0][0] - 90, cell[0][1] - 115);\n          context.stroke();\n          context.restore();\n          return true;\n        }\n      }, // }}}\n\n      plusNode: function(){\n        this.nodes+=20;\n        this.draw();\n      },\n\n      minusNode: function(){\n        this.nodes-=20;\n        this.draw();\n      }\n    }\n  }\n  </script>\n\n  <style>\n  html,\n  body {\n  }\n  .main-panel {\n    width: 100%;\n    /*position: absolute;*/\n    z-index: 2;\n  }\n  </style>\n"],"sourceRoot":"webpack://"}]);

